.projects-rect {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 80%;
    width: 80%;
    text-wrap: pretty;
    background: rgba(40,40,60,1);
    border-radius: 1.5rem;
    text-align: center;
    max-width: 1200px;
    max-height: 800px;
    color: white;
}

.thumbnail {
    width: 100%;
    height: 70%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 1.5rem;
}

.thumbnail a {
    width: 100%;      
    height: 100%;     
    object-fit: cover;
    max-height: 100%;
}

.thumbnail img {
    width: 100%;      
    height: 100%;     
    object-fit: cover;
    max-height: 100%;
}

.arrow-left {
    position: absolute;
    left: 0; 
    top: 50%;
    margin: 10px;
    padding: 10px;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 1000;
}

.arrow-right {
    position: absolute;
    right: 0; 
    top: 50%;
    margin: 10px;
    padding: 10px;
    transform: translateY(-50%); 
    cursor: pointer;
    z-index: 1000;
}

.proj-desc {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    height: 40%;
    width: 100%;
    font-size: 24px;
    padding: 10px;
    position: relative;
    overflow: hidden;
}

#proj-title {
    font-size: calc(1rem + 1.5vmin);
    font-weight: bold;
    margin-top: 10px;
    margin-left: 30px;
}

#proj-text {
    text-align: left;
    font-size: 16px;
    margin: 10px;
    padding: 20px;
}

@media (max-width: 768px) {
    .projects-rect {
        height: auto;
        width: auto;
        margin: 20px;
    }

    .proj-desc {
        height: auto;
    }

    .thumbnail {
        width: 100%;
        height: auto;
        align-items: flex-start;
        justify-content: flex-start;
    }

    .thumbnail img {
        width: 100%;
        height: auto;
        object-fit: contain;
    }
}