.contacts-rect {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 30%;
    width: 80%;
    text-wrap: pretty;
    background: rgba(64,64,145,0.1);
    border-radius: 1.5rem;
    text-align: center;
    max-width: 1200px;
    max-height: 800px;
    min-height: 128px;
}

.contacts-rect p {
    font-size: calc(1rem + 1.5vmin);
    margin: 10px;
    padding: 5px;
    color: white;
    overflow: hidden;
}

@media (max-height: 600px) {
    .contacts-rect p {
        font-size: calc(1rem + 0.5vmin);

    }
}