.footer-bar {
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,0.3);
    width: 100%;
    height: 5%;
    min-height: 20px;
    max-height: 100px;
    z-index: 1;

}

.footer-text {
    color: white;
    font-size: calc(15px + 0.1vmin);    
}