.home-body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0;
    padding: 0;
    background-color: rgba(36, 3, 63, 0.836);
    background-image: url("data:image/svg+xml,%3Csvg width='80' height='80' viewBox='0 0 80 80' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%239C92AC' fill-opacity='0.12'%3E%3Cpath d='M50 50c0-5.523 4.477-10 10-10s10 4.477 10 10-4.477 10-10 10c0 5.523-4.477 10-10 10s-10-4.477-10-10 4.477-10 10-10zM10 10c0-5.523 4.477-10 10-10s10 4.477 10 10-4.477 10-10 10c0 5.523-4.477 10-10 10S0 25.523 0 20s4.477-10 10-10zm10 8c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8zm40 40c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8z' /%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
    width: 100%;
    height: 100%;
    min-width: 25dvw;
    z-index: 0;
    overflow: auto !important;
}

.home-body-rect {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    height: 80%;
    width: 80%;
    background-color: rgba(24, 1, 43, 0.692);
    background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11 18c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm48 25c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm-43-7c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm63 31c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM34 90c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm56-76c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM12 86c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm28-65c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm23-11c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-6 60c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm29 22c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zM32 63c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm57-13c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-9-21c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM60 91c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM35 41c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM12 60c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2z' fill='%2366439f' fill-opacity='0.4' fill-rule='evenodd'/%3E%3C/svg%3E");
    border-radius: 1.5rem;
    text-align: center;
    max-width: 800px;
    max-height: 800px;
    margin: 5px;
    padding: 10px;
}

.about-text {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 32px;
    height: 15%;
    margin-top: 10px;
    width: 100%;
    text-shadow: 0 4px 20px rgba(255,255,255,0.8);
}

.home-content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    color: white;
    font-size: 24px;
    height: 80%;
    width: 80%;
}

.home-content-img {
    width: 100%;
    height: 10%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding: 10px;
}

.home-content-img a {
    min-width: 34px;
    min-height: 34px;
    max-width: 124px;
    max-height: 124px;
    width: 40px;
    height: 40px;
    margin: 5px;
}

.home-content-img img {
    min-width: 34px;
    min-height: 34px;
    max-width: 124px;
    max-height: 124px;
    width: 40px;
    height: 40px;
    margin: 5px;
}

.home-content-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column; /* Ensures proper stacking of paragraphs */
    width: 100%;
    max-height: 80%; /* Restrict the height to avoid overflow */
    overflow-y: auto; /* Enables scrolling when content overflows */
    color: white;
    font-size: calc(24px - 0.4vmin);
    margin-bottom: 0;
    letter-spacing: 2px;
    padding: 10px; /* Add some padding for better readability */
    scrollbar-width: thin; /* For Firefox */
}

.home-content-text::-webkit-scrollbar {
    width: 5px; /* Custom scrollbar width */
    background: rgba(30, 7, 39, 0.5); /* Custom scrollbar color */
}

.home-content-text::-webkit-scrollbar-thumb {
    background: rgba(30, 7, 39, 0.5); /* Custom scrollbar color */
    border-radius: 4px;
}

.home-content-text::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.7); /* Hover effect for scrollbar */
    background: rgba(30, 7, 39, 0.5); /* Custom scrollbar color */
}

.home-content-text-box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
