.sticky-nav-bar {
    position: absolute;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;    
    background: rgba(30,40,70,0.3);
    border-bottom-left-radius: 64px;
    border-bottom-right-radius: 64px;
    width: 100%;
    height: 5%;
    gap: 25px;
    max-height: 250px;
    min-height: 50px;
    z-index: 1000;
    margin: 0;
    padding: 0;
}

.sticky-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    padding: 2px;
    margin: 5px;
    cursor: pointer;
}

.sticky-nav a {
    color: white;
    text-decoration: none;
    font-weight: bold;
}

.sticky-nav a:hover {
    text-shadow: 0px 0px 15px rgba(255,255,255,0.8);
}